<template>
  <div class="content">
    <el-row class="title">
      企业信息审核
    </el-row>
    <el-row class="clientClass">
      <el-radio-group size="large" v-model="filter.type">
        <el-radio-button  label="0">未审核</el-radio-button>
        <el-radio-button  label="1">审核通过</el-radio-button>
      </el-radio-group>
    </el-row>
    <el-row class="listContent"  v-loading="viewControl.isListLoading">
      <el-table
        :data="listData"
        border
        style="width: 100%">
        <el-table-column
          label="客户所在公司"
          width="400">
          <template scope="scope">
            <el-autocomplete
              class="clearNameInput"
              v-model="listData[scope.$index].company_name"
              :fetch-suggestions="searchCompanyName"
              placeholder="请输入内容"
              @select="autoCompany(arguments[0],scope.$index,'company_id')"
            :disabled="listData[scope.$index].aDisable&&filter.type==0"></el-autocomplete>
          </template>
        </el-table-column>
        <el-table-column
          label="客户集团总公司"
          width="400">
          <template scope="scope">
            <el-autocomplete
              class="clearNameInput"
              v-model="listData[scope.$index].group_company_name"
              :fetch-suggestions="searchCompanyName"
              placeholder="请输入内容"
              :disabled="listData[scope.$index].dDisable&&filter.type==0"
              @select="autoCompany(arguments[0],scope.$index,'group_company_id')"></el-autocomplete>
          </template>
        </el-table-column>
        <el-table-column
          class="tcenter"
          label="操作">
          <template scope="scope">
            <div class="tleft">
              <el-button
                type="primary"
                size="small"
                :loading="viewControl.isSubmiting"
                :disabled="listData[scope.$index].editCount<=0&&filter.type==1"
                @click="submitAudit(listData[scope.$index],scope.$index)">{{filter.type==0?'审核通过':'再次审核'}}</el-button>
              <span class="ItemErr">{{listData[scope.$index].errInfo}}</span>
            </div>
          </template>
        </el-table-column>
      </el-table>
  <!--    <el-row class="emptyList" v-if="listData.length<=0&&!viewControl.loadingList">
        暂无更多信息
      </el-row>-->
      <el-row class="pagerBox">
        <el-pagination
          @size-change="pagerSizeChange"
          @current-change="pagerChange"
          :page-size="20"
          :current-page="listStatus.current_page"
          layout="total, prev, pager, next"
          :total="listStatus.total"></el-pagination>
      </el-row>
    </el-row>
  </div>
</template>
<script>
  import {
    Radio,
    RadioButton,
    RadioGroup,
    Row,
    Badge,
    FormItem,
    Form,
    Input,
    Button,
    Pagination,
    Notification,
    Autocomplete
  } from 'element-ui';
  import Vue from 'vue'
  Vue.prototype.$notify=Notification
  export default {
    name:'',
    data(){
      return{
        viewControl:{
          isSubmiting:false,
          isListLoading:false,
        },
        filter:{
          type:0
        },
        listStatus:{
          current_page:1,
          total:1,
        },
        listData:[]
      }
    },
    mounted() {
      this.getListData()
    },
    methods:{
      autoCompany(item,index,name){
        this.listData[index][name]=item.id;

        console.log(this.listData[index])
        this.listData[index].editCount++;
        if (name == 'group_company_id'){
          this.listData[index]['cache_group_company_name']=item.value;
          this.listData[index].company_id==0?this.listData[index].errInfo='请选择客户所在公司':this.listData[index].errInfo=''
        }
        if (name == 'company_id'){
          this.listData[index]['cache_company_name']=item.value;
          if (this.listData[index].group_company_name!=''){
            this.listData[index].group_company_id==0?this.listData[index].errInfo='请选择客户集团总公司':this.listData[index].errInfo=''
          }
        }
      },
      getListData(){
        let self = this;
        let filter = self.listStatus;
        filter.type = self.filter.type;
        self.viewControl.isListLoading = true;
        self.$ajax.get(self.$api.getModuleHost('CRM', "PretreatmentCustomer/reviewCompanyList"),
          filter)
          .then(res => {
            for (let p in self.listStatus){
              self.listStatus[p]=res[p]
            }
            let list = res.data;
            if(list instanceof Array&&list.length>0){
              for (let i = 0;i<list.length;i++){
                list[i].errInfo='',
                list[i].editCount=0,
                  //用来对比最后选择的公司名是否被修改过。
                list[i].cache_company_name =  list[i].company_name
                list[i].cache_group_company_name =  list[i].group_company_name
                //结束
                list[i].company_id!=0?list[i].aDisable=true:list[i].aDisable=false;
                list[i].group_company_id!=0?list[i].dDisable=true:list[i].dDisable=false;
              }
            }
            self.listData=list;
            self.viewControl.isListLoading = false;
          })
          .catch(err => {
            console.log(err)
            self.viewControl.isListLoading = false;
          })
      },
      searchCompanyName(queryString,rs){
        let self = this;
        self.$ajax.get(self.$api.getModuleHost('CRM', "PretreatmentCustomer/getCompanyList"),
          {company_name:queryString})
          .then(res => {
            let queryArr = [];
            let list = res.data;
            if (list.length>0){
              for (let i = 0 ; i < list.length ; i++){
                let obj = {};
                obj.value = list[i].name;
                obj.id = list[i].id;
                obj.alias = list[i].alias;
                queryArr.push(obj)
              }
            }
           rs(queryArr)
          })
          .catch(err => {
            console.log(err)
          })
      },
      pagerSizeChange(){
      },
      pagerChange(index){
        this.listStatus.current_page = index;
        this.getListData()
      },
      submitAudit(item,index){
        let self = this;
        console.log(item.company_id,item.company_name,item.cache_company_name)
        if (item.company_id==''||item.company_id==0||item.company_name!=item.cache_company_name){
          self.listData[index].errInfo='请选择客户所在公司';
          return;
        }
        if((item.group_company_id==''||item.group_company_id==0)&&item.group_company_name!='') {
          self.listData[index].errInfo='请选择客户集团总公司';
          return;
        }
        self.viewControl.isSubmiting = true;
        self.$ajax.post(self.$api.getModuleHost('CRM', "PretreatmentCustomer/reviewCompany"),
          {
            id:item.id,
            company_id:item.company_id,
            company_name:item.company_name,
            group_company_id:item.group_company_id,
            group_company_name :item.group_company_name
          })
          .then(res => {
            if (res.code == 1000){
              self.listData[index].editCount=0;
              if (self.filter.type==0){
                self.listData.splice(index,1);
              }
              self.$notify({
                title: res.msg,
                message: res.msg,
                type: 'success'
              })
              self.viewControl.isSubmiting = false;
            }else {
              self.viewControl.isSubmiting = false;
              self.$notify({
                title: res.msg,
                message: res.msg,
                type: 'warning'
              })
            }
          })
          .catch(err => {
            self.getListData();
            console.log(err)
          })
      },
      companyChange(){
        console.log('-------------')
      }
    },watch:{
      'filter.type'(){
        this.listStatus.current_page = 1;
        this.getListData()
      }
    },
    components:{
      ElAutocomplete:Autocomplete,
      ElButton:Button,
      ElInput:Input,
      ElForm:Form,
      ElFormItem:FormItem,
      ElBadge:Badge,
      ElRow:Row,
      ElPagination:Pagination,
      ElRadioButton:RadioButton,
      ElRadioGroup:RadioGroup,
      ElRadio:Radio,
      ELNotification:Notification
    }
  }
</script>
<style lang="less">
  .content{
    padding:30px;
  }
  .title{
    font-size:20px;
    padding-bottom:20px;

  }
  .clientClass{
    padding-bottom:15px;
    border-bottom:1px solid #1D8CE0;
  }
  .listContent{
    width:1130px;
    padding-top:10px;
  }
  .clearNameInput{
    width:100%;
  }
  .tcenter{
    text-align:center;
  }
  .tleft{
    text-align:left;
  }
  .el-table th>.cell{
    text-align:center;
  }
  .ItemErr{
    font-size:12px;
    color:red;
  }
  .pagerBox{
    padding:15px 0;
  }
  .el-pagination{
    display:inline-block;
    float:right;
  }
  .emptyList{
    text-align:center;
    font-size:16px;
    line-height:100px;
    min-height:100px;
  }
</style>



